//vue

import Vue from 'vue';
import Vuex from 'vuex';
import mutations from './mutations.js';
import actions from './actions.js';
//
Vue.use(Vuex);

//store
const store = new Vuex.Store({
    //
    state: {
        topics: [
            { name: "all", desc: "" },
            { name: "good", desc: "" },
            { name: "share", desc: "" },
            { name: "ask", desc: " " },
            { name: "job", desc: "" },
            { name: "dev", desc: "" }
        ],
        // Actionsheet
        // 
        unLogindmoreMenus: {
            login: "",
        },
        // 
        logindmoreMenus: {
            create:'',
            center: "",
            msg: "",
            logout: ""
        },
        // 
        isLogined: false,
        // 
        loginname: '',
        // accesstoken
        accesstoken:'',
        //id
        author_id:'',
        // Actionsheet
        isShowMoreMenus: false
    },
    //
    getters: {
        moreMenus: function (state) {
            if (state.isLogined) {
                // 
                return state.logindmoreMenus;
            }
            return state.unLogindmoreMenus;
        },
        getTpoicDescByTab:function(state){
            return function(tab){
                for(var i=0;i<state.topics.length;i++){
                    if(state.topics[i].name == tab){
                        return state.topics[i].desc;
                    }
                }
            }
        }
    },
    //
    mutations,
    //
    actions,

})
//
export default store;


